.offer-text-block__item {
    padding: 100px 20px 0px 20px;
    width: 33%;
  }
  .offer-text-block__item_last {
    padding: 100px 0px 0px 0px;
  }
  .offer-text-block__item_title {
    padding: 0px 0px 10px 0px;
    font-weight: 700;
    font-size: 30px;
    line-height: 34px;
    text-align: center;
    letter-spacing: 0.055em;
  }
  .offer-text-block__item_main-text {
    padding: 0px 0px 160px 0px;
    font-weight: 300;
    font-size: 26px;
    line-height: 30px;
    text-align: center;
    letter-spacing: 0.055em;
  }
  .offer-text-block__item_icon {
    text-align: center;
    padding: 0px 0px 37px 0px;
  }
  @media (max-width: 760px) {
    .offer-text-block__item {
      padding: 30px 30px 0px 30px;
      width: 100%;
    }
  }
  